html, body {
  background-color: #20232a;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.sidebar {
  opacity: 1;
  z-index: 10000;
  background: #312450;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 15em;
  height: 100vh;
  padding: 0 2.5em;
  font-family: Poppins, sans-serif;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.sidebar ul {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.sidebar li {
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  color: #b3a6d4;
  text-align: right;
  margin: 50px 0;
  padding: 10px 0;
  font-size: .7rem;
  font-weight: 500;
  transition: color .3s;
  position: relative;
}

.sidebar li:after {
  content: "";
  background: #ffffff26;
  width: 100%;
  height: 1px;
  transition: background .4s, opacity .3s;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.sidebar li:hover, .sidebar li.active {
  color: #fff;
}

.sidebar li.active:after {
  opacity: 1;
  background: linear-gradient(to right, #fff);
}

.home-container {
  color: #fff;
  font-family: Poppins, sans-serif;
  overflow-x: hidden;
}

.welcome-section {
  text-align: left;
  background-color: #5e42a6;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 0 2rem;
  display: flex;
}

.welcome-content {
  max-width: 600px;
}

.welcome-content h1 {
  margin-bottom: 1rem;
  font-size: 3rem;
  font-weight: 700;
}

.welcome-content p {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  line-height: 1.6;
}

.learn-more-btn {
  color: #fff;
  cursor: pointer;
  background: none;
  border: 2px solid #fff;
  border-radius: 30px;
  padding: .8rem 1.5rem;
  font-size: 1rem;
  transition: all .3s;
}

.learn-more-btn:hover {
  color: #5a36cc;
  background: #fff;
}

.about-section {
  text-align: center;
  background: #1e1e2f;
  min-height: 100vh;
  padding: 6rem 2rem;
}

.about-section h2 {
  color: #e6e6e6;
  margin-bottom: 1.5rem;
  font-size: 2.5rem;
}

.about-section p {
  color: #ccc;
  max-width: 700px;
  margin: 0 auto;
  font-size: 1.2rem;
  line-height: 1.7;
}
