html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: #20232a; /* optional, just to match sidebar background */
}


.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 15em;
  background: #312450; /* dark purple */
  /* color: #b3a6d4; */
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;  /* vertically center menu */
  align-items: flex-start;  /* text aligned left */
  padding: 0 2.5em;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
  overflow-y: hidden;
  z-index: 10000;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.sidebar li {
  position: relative;
  margin: 50px 0;
  padding: 10px 0;
  font-size: 0.70rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: 500;
  cursor: pointer;
  color: #b3a6d4;
  transition: color 0.3s ease;
  text-align: right;
}

/* Faint underline always visible */
.sidebar li::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 100%;
  height: 1px;
  background: rgba(255, 255, 255, 0.15);
  transition: background 0.4s ease, opacity 0.3s ease;
}

/* Hover or active text */
.sidebar li:hover,
.sidebar li.active {
  color: #ffffff;
}

/* Active underline */
.sidebar li.active::after {
  background: linear-gradient(to right, white);
  opacity: 1;
}





/* side page welcome  */
/* General page layout */
.home-container {
  font-family: "Poppins", sans-serif;
  color: #fff;
  overflow-x: hidden;
}

/* Welcome Section */
.welcome-section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: linear-gradient(135deg, #5a36cc, #7a4de0); */
  background-color: #5e42a6;
  text-align: left;
  padding: 0 2rem;
}

.welcome-content {
  max-width: 600px;
}

.welcome-content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.welcome-content p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.learn-more-btn {
  padding: 0.8rem 1.5rem;
  background: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 30px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.learn-more-btn:hover {
  background: #fff;
  color: #5a36cc;
}

/* About Section */
.about-section {
  background: #1e1e2f;
  min-height: 100vh;
  padding: 6rem 2rem;
  text-align: center;
}

.about-section h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: #e6e6e6;
}

.about-section p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.7;
  color: #ccc;
}
